import { TextField } from "@rp/react/web/mui/core"
import { useFeedbackContext } from "./FeedbackContext"
import { FeedbackForm, FormControlStyled } from "./FeedbackMode.styles"

export default function QueryDescriptionForm() {
  const { description, setDescription } = useFeedbackContext()
  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setDescription(event.target.value)
  }
  return (
    <FeedbackForm>
      <FormControlStyled>
        <TextField
          disabled
          value={description}
          label="Query Description"
          onChange={handleChange}
          inputProps={{
            "data-testid": "query-description-input",
          }}
          multiline
          rows={4}
        />
      </FormControlStyled>
    </FeedbackForm>
  )
}
